.no-scroll {
  overflow: hidden;
}
div.kloudless-meeting-scheduler div.kloudless-meeting-scheduler-container {
  width: 515px;
  margin: 0 auto;
  height: 695px;
  overflow: hidden;
  border: 0;
  padding: 0;
  border-radius: 10px;
}
div.kloudless-meeting-scheduler div.kloudless-meeting-scheduler-container > iframe.kloudless-meeting-scheduler-iframe {
  background: transparent;
  border: 0;
  padding: 0;
  overflow: hidden;
  width: 100%;
  height: 100%;
}
@media screen and (max-width: 515px) {
  div.kloudless-meeting-scheduler div.kloudless-meeting-scheduler-container {
    width: 100%;
  }
}
div.kloudless-meeting-scheduler div.kloudless-meeting-scheduler-modal {
  overflow: hidden;
  position: fixed;
  z-index: 9999;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  background: transparent;
}
div.kloudless-meeting-scheduler div.kloudless-meeting-scheduler-modal > div.kloudless-meeting-scheduler-modal-overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: black;
  opacity: 0.46;
  z-index: 999;
}
div.kloudless-meeting-scheduler div.kloudless-meeting-scheduler-modal > div.kloudless-meeting-scheduler-container {
  position: relative;
  margin: 0 auto;
  height: 95%;
  top: 2.5%;
  z-index: 1000;
}
